.Default-block{
    margin: 60px 0 0 0;
    text-align: left;
    h1{
        font-weight: bold;
        font-size: 80px;
        line-height: 1.5;
        @media only screen and (max-width : 768px) {
            font-size: 60px;
        }
    }
    p{
        font-size: 20px;
        letter-spacing: 3px;
        line-height: 1.5;
        margin: 5px 0 0 0;
    }
}